    text-align: left;
    width: 49%;
    display: inline-block;
    margin-bottom: 5px; }
  .detalle-pago-subasta .tdec-form input.form-control {
    font-size: 12px;
    font-size: 12px;
    border: 1px solid #85d0d6;
    border-radius: 0px; }
