.generic-content {
  color: #000;
  background-color: #fff;
  padding: 20px 30px;
  margin-top: 50px;
  text-align: justify;
  min-height: 300px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px; }
  .generic-content .content-title {
    color: #d5a00f;
    text-transform: uppercase; }
  .generic-content .content-body {
    color: #747474; }
  .generic-content .panel-left {
    padding-top: 20px; }
  @media (min-width: 992px) {
    .generic-content .panel-left {
      padding-top: 0; } }
  @media (max-width: 768px) {
    .generic-content .content-title {
      font-size: 22px; }
    .generic-content .content-body h3 {
      font-size: 18px;
      line-height: 24px; } }
