.galeria-detalle {
  max-height: none; }

.section-room {
  /*
    {
        border-color: #ea9f2e;
    }
    */ }
  .section-room .banner-container.mini-gallery {
    width: 100%;
    max-width: 500px; }
  .section-room .banner-container.mini-gallery .banner {
    height: 170px !important; }
  .section-room .text-white {
    color: #fff; }
  .section-room .mejor-postor, .section-room .mini-gallery {
    padding: 30px 0px 50px; }
  .section-room .subasta-tiempo {
    font-size: 18px; }
  .section-room .banner-info:first-child, .section-room .banner-info:last-child {
    height: auto; }
  .section-room .ofertas-restantes {
    color: #fff;
    margin-top: 10px;
    font-size: 20px;
    text-transform: none; }
  .section-room .contenedor-indicadores {
    background-color: #00bcb6;
    color: #fff;
    padding: 0;
    padding-bottom: 10px; }
  .section-room .contenedor-indicadores h3 {
    text-align: center;
    margin-bottom: 20px; }
  .section-room .indicador-contenedor {
    text-align: center; }
  .section-room .indicador-subasta-externo {
    margin: 0 auto;
    width: 75px;
    height: 75px;
    border: solid 5px;
    padding: 5px;
    margin-bottom: 5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease; }
  .section-room .indicadores-oportunidades {
    padding-top: 50px; }
  .section-room #total-bids.active,
  .section-room #lasting-bids.active {
    background-color: #00bcb6; }
  .section-room #fault-bids.active {
    background-color: #fb5f60; }
  .section-room #towin-bids.active {
    background-color: #95c630; }
  .section-room .indicador-subasta {
    color: #747474;
    display: block;
    width: 55px;
    height: 55px;
    background-color: #fff;
    margin: 0 auto 10px;
    font-size: 20px;
    line-height: 55px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease; }
  .section-room .indicador-timer-externo {
    border: solid 3px #fff;
    margin-top: 20px;
    padding: 5px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px; }
  .section-room .indicador-timer {
    font-size: 20px;
    text-align: center;
    background-color: #fff;
    color: #d5a00f;
    padding: 2px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px; }
  .section-room .texto-gris {
    color: #747474; }
  .section-room .total-subastado p {
    font-size: 25px;
    margin: 0; }
  .section-room .ngrs-range-slider .ngrs-handle {
    top: -15px;
    height: 50px; }
  .section-room .ngrs-range-slider .ngrs-join {
    background-image: none !important;
    background-color: #00bcb6 !important; }
  .section-room .ngrs-range-slider .ngrs-handle-max i {
    background-image: none; }
  .section-room .tp-rightarrow.default,
  .section-room .tp-leftarrow.default {
    display: none; }
  .section-room .rebase-izq,
  .section-room .rebase-der {
    height: 148px;
    bottom: -22px;
    background-position: center -5px; }
  @media (max-width: 992px) {
    .section-room .precio-rango {
      font-size: 24px;
      margin: 0 5px; }
    .section-room .usr-postor {
      font-size: 24px; }
    .section-room .precio-subasta {
      font-size: 24px; }
    .section-room .rebase-izq,
    .section-room .rebase-der {
      height: 189px;
      bottom: -19px;
      background-position: center -10px; }
    .section-room .banner {
      height: 200px !important; } }
  @media (max-width: 768px) {
    .section-room .ngrs-range-slider {
      width: 250px !important; }
    .section-room .subasta-tiempo {
      font-size: 16px; }
    .section-room .indicador-contenedor {
      margin-top: 15px; }
    .section-room .panel-oferta {
      padding: 15px 0; }
    .section-room .hey {
      font-size: 38px; }
    .section-room .msje-postor {
      font-size: 16px; }
    .section-room .banner-hidden {
      display: block !important; }
    .section-room .rebase-izq,
    .section-room .rebase-der {
      height: 138px;
      bottom: -22px;
      background-position: center -10px; } }
  @media (max-width: 405px) {
    .section-room .rebase-izq,
    .section-room .rebase-der {
      height: 180px;
      bottom: -23px; } }
